import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Spin, Typography } from 'antd';

const { Title, Paragraph } = Typography;

const TenderDetails = () => {
  const { id } = useParams();
  const [tenderData, setTenderData] = useState(null);

  useEffect(() => {
    const fetchTenderData = async () => {
      try {
        const response = await fetch(`https://public.api.openprocurement.org/api/2.5/tenders/${id}`);
        const data = await response.json();
        setTenderData(data);
      } catch (error) {
        console.error('Error fetching tender data:', error);
      }
    };

    fetchTenderData();
  }, [id]);

  if (!tenderData) {
    return <Spin />;
  }

  //     procuringEntity
  const procurementEntity = tenderData.data.procuringEntity;
  const procurementEntityName = procurementEntity ? procurementEntity.name : 'Unknown';

  const { tender, budget, classification, items } = tenderData.data;

  return (
<div >
 
  <Title level={1}>ID: {id}</Title>
  
  <Paragraph> : {procurementEntityName}</Paragraph>
  {tender && tender.tenderPeriod && (
    <div key="tender-period">
      <Paragraph>  : {tender.tenderPeriod.startDate}</Paragraph>
    </div>
  )}
  {budget && (
    <div key="budget">
      <Paragraph>  : {budget.amount}</Paragraph>
    </div>
  )}
  {classification && (
    <div key="classification">
      <Paragraph>: {classification.description}</Paragraph>
    </div>
  )}
  {/*    , , items */}
  <Title level={2}>  :</Title>
  {items && items.length > 0 ? (
    items.map((item) => (
      <div key={item.id}>
        <Paragraph strong>: {item.description}</Paragraph>
        {/*     */}
        {item.unit && <Paragraph> : {item.unit.name}</Paragraph>}
        <Paragraph>: {item.quantity}</Paragraph>
        {item.classification && (
          <Paragraph>: {item.classification.description}</Paragraph>
        )}
        {item.additionalClassifications && item.additionalClassifications.length > 0 && (
          <Paragraph>
             : {item.additionalClassifications[0].description}
          </Paragraph>
        )}
      </div>
    ))
  ) : (
    <Paragraph>  .</Paragraph>
  )}
</div>
  );
};

export default TenderDetails;


