import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { Spin, Typography } from 'antd';

const { Title, Paragraph } = Typography;

const PlansDetails = () => {
  const { id } = useParams();
  const [tenderData, setTenderData] = useState(null);

  useEffect(() => {
    const fetchTenderData = async () => {
      try {
        const response = await fetch(`https://public.api.openprocurement.org/api/2.5/plans/${id}`);
        const data = await response.json();
        setTenderData(data);
      } catch (error) {
        console.error('Error fetching tender data:', error);
      }
    };

    fetchTenderData();
  }, [id]);

  if (!tenderData) {
    return <Spin />;
  }

  //     procuringEntity
  const procurementEntity = tenderData.data.procuringEntity;
  const procurementEntityName = procurementEntity ? procurementEntity.name : 'Unknown';

  //     
  const { tender, budget, classification, items } = tenderData.data;

  return (
    <div>
      <Title level={1}>ID: {id}</Title>      
      <Paragraph> : {procurementEntityName}</Paragraph>
      <Paragraph>  : {tender.tenderPeriod.startDate}</Paragraph>
      <Paragraph>  : {budget.amount}</Paragraph>
      <Paragraph>: {classification.description}</Paragraph>      
      <Title level={2}>  :</Title>
      {items && items.length > 0 ? (
        items.map((item) => (
          <div key={item.id}>
            <Paragraph strong>: {item.description}</Paragraph>
           </div>
        ))
      ) : (
        <Paragraph>  .</Paragraph>
      )}
    </div>
  );
};

export default PlansDetails;


